.card {
    border: 1px solid black;
    width: 230px;
    padding: 20px;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    text-align: center;
    margin-top: 20px;
}


.num {
    height: 40px;
    width: 40px;

    margin: 10px;
    background-color: rgb(14, 66, 66);
    color: white;

    display: flex;
    justify-content: center;
    align-items: center;
}

.holder {
    margin: 30px auto;
    width: 300px;
    display: flex;
    flex-direction: column;
    align-items: center;
}